.header .logo{
  margin: 30px 0 0 30px;
  background-color: rgba(0,0,0,0.8);
  color: #fff;
  padding: 10px;
  border-radius: 20px;
  text-transform: capitalize;
}
ul,li{
  list-style: none;
  margin: 0;
  padding: 0;
}

/* 네비게이션 디자인 */
.circular-menu{
  position: fixed;
  right: 1em;
  bottom: 1em;
}

.circular-menu .floationg-btn{
  display: block;
  width: 4.5em;
  height: 4.5em;
  border-radius: 50%;
  background-color: #16a085;
  box-shadow:  0 2px 5px 0 rgba(0,0,0,0.3);
  color: #666;
  text-align: center;
  cursor: pointer;
  outline: 0;
  border: 1px solid #333;
}
.circular-menu.on .floationg-btn{
  box-shadow: inset 2px 2px 5px 2px rgb(0 0 0 / 30%);
}
.circular-menu .floationg-btn:active{
  box-shadow: 0 4px 8px 0 rgba(0,0,0,0.4);
  background-color: #1abc9c;
}
.circular-menu .floationg-btn i {
  font-size: 1.3em;
  transition:  all 0.2s;
}
.circular-menu.on .floationg-btn{
  background-color: #1abc9c;
}
.circular-menu.on .floationg-btn i{
  transform: rotate(-45deg);
  font-size: 2em;
  color: #333;  
}
.circular-menu::after{
  display: block;
  content: '';
  width: 3.5em;
  height: 3.5em;
  border-radius: 50%;
  position: absolute;
  z-index: -2;
  background-color: #1abc9c;
  transition:  all 0.3s ease;

  top: 300px;
  right: -300px;
}

.circular-menu.on::after{
  transform: scale(5.5);

  top: 0;
  right: 0;
}

.circular-menu .item-wrapper {
  padding: 0;
  margin: 0;
}
.circular-menu .menu-item {
  position: absolute;
  top: 0.2em;
  right: 0.2em;
  z-index: -1;
  display: block;
  text-decoration: none;
  color:#fff;
  font-size: 1.2em;
  height: 3em;
  text-align: center;
  transition: all 0.3s ease;
}
.circular-menu .menu-item.active{
  color: #8e44ad;
}
.circular-menu .item-wrapper li .menu-item{
  z-index: -3;
  top: 100px;
  right: -100px;
}
.circular-menu.on .item-wrapper li .menu-item{
  width: auto;
  z-index: -1;
  top: 0.2em;
  right: 0.2em;
}
.circular-menu .menu-item:hover{
  background-color: rgba(0,0,0,0.3);
  border-radius: 50%;
}

.circular-menu.on .item-wrapper li:nth-child(1) .menu-item{
  transform: translate(0em , -6em)
}
.circular-menu.on .item-wrapper li:nth-child(2) .menu-item {
  transform: translate(-2.5em , -4em)
}
.circular-menu.on .item-wrapper li:nth-child(3) .menu-item {
  transform: translate(-4em , -1em)
}

.menu-item span{
  position: relative;
  top: 50%;
  display: block;
  transform: translateY(-50%);
}
