
#visual{
  font-family: 'Roboto Slab', serif;
  display: flex;
  align-items: center;
  justify-content: center;
  background-color: #333;
  height: 100vh;
  background:  url("https://source.unsplash.com/1920x1080/?denver");
  background-position: center;
  background-repeat: no-repeat;
  
}

.card{
  background-color: rgba(0,0,0,0.7);
  padding: 3em;
  border-radius: 30px;
  width: 100%;
  max-width: 600px;
  color: #fff;
  font-weight: 400;
  font-family: 'Noto Sans KR' ,sans-serif;
}
.description{
  text-transform: capitalize;
}
.flex{
  display: flex;
  align-items: center;
  justify-content: center;
  font-size: 2rem;
}
.weather {
  margin-top: 30px;
}
.header {
  position: fixed;
  top: 0;
  left: 0;
}

h2.city {
  font-size: 2.5rem;
  margin-bottom: 20px;
  font-weight: 700;
}

.humidity {
  padding: 20px;
  font-size: 1.8rem;
}

.temp {
  font-size: 2.5rem;
  font-weight: 600;
  margin-bottom: 20px;
}
.wind {
  font-size: 1.8rem;
}

.card input#search-bar {
  outline: none;
  border: none;
  padding: 10px;
  border-radius: 1.5rem;
  line-height: 2rem;
  width: 50%;
  text-indent: 20px;
  font-size: 1.3rem;
}

.card .search {
  width: 100%;
}


.card button {
  border: none;
  padding: 10px;
  line-height: 2rem;
  border-radius: 2rem;
  font-size: 1.2rem;
  margin-left: 20px;
  width: auto;
  background: rgba(255,255,255,0.5);
  text-transform: capitalize;
  cursor: pointer;
  transition: all 0.2s ease;
  box-shadow: 2px 2px 5px 3px #000;

}
.card button:active{
  background: rgba(255,255,255,0.8);
  box-shadow: inset 2px 2px 5px 3px rgba(0,0,0,0.7);
}

.card button:hover{
  background: rgba(255,255,255,0.8);

}